<template>
  <div class="page flex-col">
    <PC v-if="isPc" :qrUrl="qrUrl"></PC>
    <Phone v-else :qrUrl="qrUrl"></Phone>
  </div>
</template>
<script>
import PC from './pc.vue'
import Phone from './phone.vue'
import { downLoad } from './downLoad'
export default {
  components: { PC, Phone },
  mixins: [downLoad],
  data () {
    return {
      isPc: true
    };
  },
  mounted () {
    this.PcOrPhone()
    this.ChangeOnresize()
    window.addEventListener('resize', () => {
      this.PcOrPhone()
      this.ChangeOnresize()
    }, false)
  },
  methods: {
    PcOrPhone () {
      let groupW = document.body.clientWidth
      let userAgentInfo = navigator.userAgent;
      let Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
      let getArr = Agents.filter(i => userAgentInfo.includes(i));
      this.isPc = getArr.length ? false : true
      if (groupW <= 768) {
        this.isPc = false
      } else {
        this.isPc = true
      }
    },
    ChangeOnresize () {
      let groupH = document.body.scrollHeight
      var el = document.querySelectorAll('.g_h')
      for (let i in el) {
        if (el[i].style) {
          el[i].style.setProperty('--scrollHeight', groupH + 'px')
        }
      }
    },
    tabsChange (res) {
      if (this.active === res) return
      this.active = res
      this.$refs.swipe.swipeTo(res)
    }
  },
};
</script>
<style scoped>
@import url("./index.css");
@import url("./common.css");
</style>
