<script>
export default {
  props: ['qrUrl'],
  data () {
    return {
      show: false,
      active: 0,
      showPopover: false,
      actions: [
        {
          text: '首頁',
          id: 'index'
        },
        {
          text: '服務',
          id: 'serve'
        },
        {
          text: '里程碑',
          id: 'lichengbei'
        },
        {
          text: '團隊',
          id: 'team'
        },
        {
          text: '常見問題',
          id: 'wenti'
        }
      ],
      activeName1: '',
      activeName2: '',
      activeName3: '',
      activeName4: '',
      percent: 0,
      scroll: 0,
      tabList: [
        {
          name: 'Web3錢包'
        },
        {
          name: '項目撮合'
        },
        {
          name: '短視頻直播'
        },
        {
          name: '聊天'
        },
      ],
      loopData0: [
        {
          lanhuimage0:
            require('../assets/g13.png'),
          name: 'Alexander',
          name1: 'CTO'
        },
        {
          lanhuimage0:
            require('../assets/g14.png'),
          name: 'Bahari Bahari',
          name1: '首席執行官'
        },
        {
          lanhuimage0:
            require('../assets/g15.png'),
          name: 'Eskarne',
          name1: '首席行銷官'
        },
      ],
    };
  },
  computed: {
    changeColor () {
      let percen = this.percent;
      return {
        'background-color': `rgba(0,0,0, ${percen})`,
      };
    },
  },
  mounted () {
    window.addEventListener("scroll", this.menu)
  },
  methods: {
    menu () {
      this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
      if (this.scroll < 400) {
        this.percent = (this.scroll / 100);
      } else {
        this.percent = 1;
      }
    },
    select (item) {
      this.show = false
      let element = document.getElementById(item.id);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    },
    downLoad () {
      let url = ''
      if (!this.qrUrl) {
        url = 'http://live.starshow.cc/app-release.apk'
      }
      console.log(!this.qrUrl ? url : this.qrUrl)
      return
      window.location.href = !this.qrUrl ? url : this.qrUrl
    },
    tabsChange (res) {
      if (this.active === res) return
      this.active = res
      this.$refs.swipe.swipeTo(res)
    },
  },
};
</script>