export const downLoad = {
  data () {
    return {
      qrUrl: ''
    }
  },
  mounted () {
    this.request('/appapi/public/index.php?service=Home.getConfig', res => {
      if (res) {
        this.qrUrl = res.data.info[0].apk_url
      } else {
        this.qrUrl = 'http://live.starshow.cc/app-release.apk'
      }
    })
  },
  methods: {
    request (url, resolve, reject = () => { }) {
      let dev = 'http://192.168.3.110'
      let pro = 'http://live.starshow.cc'
      let baseUrl = process.env.NODE_ENV === 'development' ? dev : pro
      url = baseUrl + url;
      fetch(url, { method: 'GET', mode: 'cors' })
        .then((res) => res.json())
        .then((json) => {
          resolve(json);
        })
        .catch((err) => {
          reject(err);
        });
    }
  }
}