<script>
import vueQr from 'vue-qr/src/packages/vue-qr.vue'
export default {
  components: { vueQr },
  props: ['qrUrl'],
  data () {
    return {
      options: {
        licenseKey: "OPEN-SOURCE-GPLV3-LICENSE",
        //是否显示导航，默认为false
        navigation: true,
        afterLoad: this.afterLoad,
        anchors: ['page1', 'page2', 'page3', 'page4', 'page5', 'page6', 'page7'],
      },
      style: {
        transform: 'translateX(25px) translateX(-50%)'
      },
      loopData0: [
        {
          lanhuimage0:
            require('../assets/g13.png'),
          name: 'Alexander',
          name1: 'CTO'
        },
        {
          lanhuimage0:
            require('../assets/g14.png'),
          name: 'Bahari Bahari',
          name1: '首席執行官'
        },
        {
          lanhuimage0:
            require('../assets/g15.png'),
          name: 'Eskarne',
          name1: '首席行銷官'
        },
      ],
      constants: {},
      active: 0,
      bgs: 'linear-gradient(0deg, rgba(20, 49, 119, 0) 0%, #143177 100%)',
      bgIndex: 2,
      current: 0,
      tabList: [
        {
          name: 'Web3錢包'
        },
        {
          name: '項目撮合'
        },
        {
          name: '短視頻直播'
        },
        {
          name: '聊天'
        },
      ]
    };
  },
  mounted () {
    var el = document.querySelectorAll('.text_47')
    for (let i in el) {
      var height = el[i].scrollHeight
      if (el[i].style) {
        el[i].style.setProperty('--max-height', height + 'px')
      }
    }
    this.ChangeOnresize()
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.ChangeOnresize()
        });
      })();
    };
  },
  methods: {
    downLoad () {
      let url = ''
      if (!this.qrUrl) {
        url = 'http://live.starshow.cc/app-release.apk'
      }
      console.log(!this.qrUrl ? url : this.qrUrl)
      return
      window.location.href = !this.qrUrl ? url : this.qrUrl
    },
    mouseover (val) {
      this.bgIndex = val
    },
    afterLoad (origin, destination, direction, trigger) {
      switch (destination.index) {
        case 0:
          this.style.transform = 'translateX(25px) translateX(-50%)'
          break;
        case 2:
          this.style.transform = 'translateX(120px) translateX(-50%)'
          break;
        case 4:
          this.style.transform = 'translateX(230px) translateX(-50%)'
          break;
        case 5:
          this.style.transform = 'translateX(312px) translateX(-50%)'
          break;
        case 6:
          this.style.transform = 'translateX(433px) translateX(-50%)'
          break;
      }
    },
    ChangeOnresize () {
      let groupH = document.body.scrollHeight
      var el = document.querySelectorAll('.g_h')
      for (let i in el) {
        if (el[i].style) {
          el[i].style.setProperty('--scrollHeight', groupH + 'px')
        }
      }
    },
    tabsChange (res) {
      if (this.active === res) return
      this.active = res
      this.$refs.swipe.swipeTo(res)
    },
    //锚点跳转
    goAnchor (selector) {
      if (selector === '#index') {
        this.style.transform = 'translateX(25px) translateX(-50%)'
        this.$refs.fullpage.api.moveTo(1, 0)
      } else if (selector === '#serve') {
        this.style.transform = 'translateX(120px) translateX(-50%)'
        this.$refs.fullpage.api.moveTo(3)
      } else if (selector === '#licheng') {
        this.style.transform = 'translateX(230px) translateX(-50%)'
        this.$refs.fullpage.api.moveTo(5, 0)
      } else if (selector === '#team') {
        this.style.transform = 'translateX(312px) translateX(-50%)'
        this.$refs.fullpage.api.moveTo(6, 0)
      } else if (selector === '#wenti') {
        this.$refs.fullpage.api.moveTo(7, 0)
        this.style.transform = 'translateX(433px) translateX(-50%)'
      }
    },
  },
};
</script>